
html{

}
body{
  background-color: rgb(186, 186, 186)!important;
}
.ui.inverted.top.fixed.menu{
  background-image: linear-gradient(135deg,rgb(24,42,11)0%, rgba(33,138,174)69%,rgb(32,167,172)89%)!important;
}
.react-calendar{
  width:100% !important;
  border:none;
  box-shadow: 0 1px 2px 0 rgb(34 36 38 / 15%);
  border-radius: .28571429rem;
}

.react-datepicker-wrapper{
  width: 100%;
}
/*home page styles*/

.masthead {
  display: flex;
  align-items: center;
  background-image: linear-gradient(
          135deg,
          rgb(24, 42, 115) 0%,
          rgb(33, 138, 174) 69%,
          rgb(32, 167, 172) 89%
  ) !important;
  height: 100vh;
}

.masthead .ui.menu .ui.button,
.ui.menu a.ui.inverted.button {
  margin-left: 0.5em;
}

.masthead h1.ui.header {
  font-size: 4em;
  font-weight: normal;
}

.masthead h2 {
  font-size: 1.7em;
  font-weight: normal;
}

/*end home page styles*/